import { NewAudio } from "@/interfaces/NewAudio";
import { useStore } from "@/stores/Store";
import { AudioList } from "@/interfaces/AudioList";

export class TrackPlayer {
  private async playTrack(audio: NewAudio): Promise<boolean> {
    this.stopTrack(audio);
    try {
      audio.element.load();
      await audio.element.play();
      return true;
    } catch (e) {
      console.log(e);
      return false;
    }
  }

  public stopTrack(audio: NewAudio) {
    audio.element.pause();
    audio.element.currentTime = 0;
  }

  public async playMenuTrack(): Promise<void> {
    await this.playNextTrack(this.getStore().audios.menuList, true);
  }

  public async playGameTrack(): Promise<void> {
    await this.playNextTrack(this.getStore().audios.gameList, false);
  }

  public async playNextTrack(list: AudioList, loop: boolean) {
    this.stopAll([
      this.getStore().audios.menuList,
      this.getStore().audios.gameList,
    ]);

    let nextIndex = list.currentIndex + 1;
    if (nextIndex >= list.items.length) {
      nextIndex = 0;
    }

    list.items[nextIndex].element.loop = loop;

    if (await this.playTrack(list.items[nextIndex])) {
      list.currentIndex = nextIndex;
    }
  }

  public stopAll(lists: AudioList[]): void {
    for (const index in lists) {
      if (lists[index].currentIndex !== -1) {
        this.stopTrack(lists[index].items[lists[index].currentIndex]);
      }
    }
  }

  public async playStart(): Promise<void> {
    await this.playTrack(this.getStore().audios.start);
  }

  public async playHit(): Promise<void> {
    await this.playTrack(this.getStore().audios.hit);
  }

  public async playMiss(): Promise<void> {
    await this.playTrack(this.getStore().audios.miss);
  }

  private getStore() {
    return useStore();
  }
}
