export class RandomList<Type> {
  public readonly items: Type[];
  private indexes: number[];

  constructor(items: Type[]) {
    this.items = items;
    this.indexes = [...Array(items.length).keys()];
    this.shuffle();
  }

  public getRandom(): Type {
    const randomIndex = this.indexes.pop();
    if (randomIndex === undefined) {
      this.indexes = [...Array(this.items.length).keys()];
      this.shuffle();
      return this.getRandom();
    }
    return this.items[randomIndex];
  }

  private shuffle(): void {
    let currentIndex = this.indexes.length;
    while (currentIndex !== 0) {
      const randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;
      [this.indexes[currentIndex], this.indexes[randomIndex]] = [
        this.indexes[randomIndex],
        this.indexes[currentIndex],
      ];
    }
  }
}
