<template>
  <div
    style="
      height: 100vh;
      display: flex;
      background-color: #000000;
      flex-direction: column;
    "
  >
    <div
      style="
        height: 100px;
        display: flex;
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
      "
    >
      <div style="margin-left: auto; display: flex">
        <div style="width: 130px; display: flex; margin-right: 20px">
          <div style="font-size: 32px; margin-top: auto; margin-bottom: auto">
            ❤️
          </div>
          <div
            style="
              margin-left: 10px;
              color: #ff3399;
              font-family: 'Saira Stencil One';
              font-size: 48px;
              margin-top: auto;
              margin-bottom: auto;
            "
          >
            {{ store.topRating }}
          </div>
        </div>
      </div>
    </div>
    <div style="margin: auto; display: flex; flex-direction: column">
      <div
        style="
          margin-left: auto;
          margin-right: auto;
          font-family: 'Creepster';
          font-size: 100px;
          transition: text-shadow 0.3s ease;
        "
        :style="{ textShadow: '0px 0px 10px ' + color }"
      >
        STALKER
      </div>
      <div style="height: 20px; display: flex">
        <div
          style="
            margin-left: auto;
            margin-right: auto;
            font-family: 'Rubik Wet Paint';
            animation: stalker-shake 0.5s;
            animation-iteration-count: infinite;
            transition: text-shadow 0.3s ease;
          "
          :style="{ textShadow: '0px 0px 10px ' + color }"
          v-if="loading"
        >
          'Погодь {{ secCount }} сек...'
        </div>
      </div>
      <div style="height: 80px">
        <div
          class="page-load-start-button"
          v-if="!loading"
          @click="onStartCLicked()"
        >
          <div
            style="
              margin: auto;
              font-family: 'Rubik Bubbles';
              font-size: 32px;
              color: white;
            "
          >
            ПОГНАЛИ!
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, onUnmounted, ref } from "vue";
import { useStore } from "@/stores/Store";
import "@fontsource/creepster";
import "@fontsource/rubik-wet-paint";
import "@fontsource/rubik-bubbles";
import "@fontsource/saira-stencil-one";
import { NewAudio } from "@/interfaces/NewAudio";
import { Sticker } from "@/interfaces/Sticker";

const store = useStore();

const color = ref<string>("#FFF");
const loading = ref<boolean>(true);
const secCount = ref<number>(0);

let colorInterval: null | number = null;

function onStartCLicked(): void {
  store.trackPlayer.playGameTrack();
  store.trackPlayer.playStart();
  store.state = "start";
}

onMounted(async function (): Promise<void> {
  secCount.value =
    store.audios.menuList.items.length +
    store.audios.gameList.items.length +
    store.stickers.items.length +
    3;
  colorInterval = setInterval(function (): void {
    color.value = store.colors.getRandom();
  }, 300);

  await loadAllResources();

  await store.trackPlayer.playMenuTrack();

  loading.value = false;
});

async function loadAllResources(): Promise<void> {
  for (const index in store.audios.menuList.items) {
    await loadResource(store.audios.menuList.items[index]);
    secCount.value--;
  }
  for (const index in store.audios.gameList.items) {
    await loadResource(store.audios.gameList.items[index]);
    secCount.value--;
  }

  await loadResource(store.audios.hit);
  secCount.value--;
  await loadResource(store.audios.miss);
  secCount.value--;
  await loadResource(store.audios.start);
  secCount.value--;

  for (const index in store.stickers.items) {
    await loadResource(store.stickers.items[index]);
    secCount.value--;
  }
}

async function loadResource(resource: NewAudio | Sticker): Promise<boolean> {
  if (resource.blob !== null) {
    return false;
  }
  const blob = await (await fetch(resource.url)).blob();
  resource.blob = blob;
  resource.element.src = URL.createObjectURL(blob);
  return true;
}

onUnmounted(function (): void {
  if (colorInterval !== null) {
    clearInterval(colorInterval);
  }
});
</script>

<style lang="scss">
@import "@/styles/common";

.page-load-start-button {
  border-radius: 20px;
  display: flex;
  margin-top: 20px;
  height: 80px;
  animation: stalker-shake 0.5s;
  animation-iteration-count: infinite;
  cursor: pointer;
  background-color: #ff3399;
  width: 300px;
  transition: box-shadow 0.3s ease;
}
</style>
