<template>
  <div style="height: 100vh; display: flex; background-color: #000000">
    <div style="margin: auto; display: flex; flex-direction: column">
      <div
        style="
          margin-left: auto;
          margin-right: auto;
          font-family: 'Creepster';
          font-size: 100px;
          transition: text-shadow 0.3s ease;
        "
        :style="{ textShadow: '0px 0px 10px ' + color }"
      >
        {{ counter === 1 ? "GO!" : counter }}
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, onUnmounted, ref } from "vue";
import { useStore } from "@/stores/Store";
import "@fontsource/creepster";

const store = useStore();

const color = ref<string>("#FFF");
const counter = ref<number>(3);

let colorInterval: number | null = null;
let counterInterval: number | null = null;

onMounted(function (): void {
  colorInterval = setInterval(function (): void {
    color.value = store.colors.getRandom();
  }, 300);
  counterInterval = setInterval(function (): void {
    if (counter.value === 1) {
      if (store.state !== "board") {
        store.state = "board";
      }
      return;
    }
    counter.value--;
  }, 1000);
});

onUnmounted(function (): void {
  if (colorInterval !== null) {
    clearInterval(colorInterval);
  }
  if (counterInterval !== null) {
    clearInterval(counterInterval);
  }
});
</script>

<style lang="scss"></style>
