<template>
  <div
    style="
      height: 100vh;
      display: flex;
      background-color: #000000;
      flex-direction: column;
    "
  >
    <div
      style="
        height: 100px;
        display: flex;
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
      "
    >
      <div style="margin-left: auto; display: flex">
        <div style="width: 100px; margin-right: 20px; display: flex">
          <div style="font-size: 32px; margin-top: auto; margin-bottom: auto">
            ⏰
          </div>
          <div
            style="
              margin-left: 10px;
              color: #ff3399;
              font-family: 'Saira Stencil One';
              font-size: 48px;
              margin-top: auto;
              margin-bottom: auto;
            "
          >
            {{ counter }}
          </div>
        </div>
        <div style="width: 130px; display: flex; margin-right: 20px">
          <div style="font-size: 32px; margin-top: auto; margin-bottom: auto">
            ❤️️
          </div>
          <div
            style="
              margin-left: 10px;
              color: #ff3399;
              font-family: 'Saira Stencil One';
              font-size: 48px;
              margin-top: auto;
              margin-bottom: auto;
            "
          >
            {{ hits - misses }}
          </div>
        </div>
      </div>
    </div>
    <div style="height: 100%; position: relative" ref="board" @click="onMiss()">
      <div
        ref="object1"
        class="board-page-object-container"
        @click.stop="onHit(1)"
      >
        <div class="board-page-object-container-object">
          <img
            width="120"
            height="120"
            ref="objectImage1"
            style="border-radius: 60px; transition: box-shadow 0.3s ease"
            :style="{ boxShadow: '0px 0px 10px ' + color1 }"
          />
        </div>
      </div>
      <div
        ref="object2"
        class="board-page-object-container"
        @click.stop="onHit(2)"
      >
        <div class="board-page-object-container-object">
          <img
            width="120"
            height="120"
            ref="objectImage2"
            style="border-radius: 60px; transition: box-shadow 0.3s ease"
            :style="{ boxShadow: '0px 0px 10px ' + color2 }"
          />
        </div>
      </div>
      <div
        ref="object3"
        class="board-page-object-container"
        @click.stop="onHit(3)"
      >
        <div class="board-page-object-container-object">
          <img
            width="120"
            height="120"
            ref="objectImage3"
            style="border-radius: 60px; transition: box-shadow 0.3s ease"
            :style="{ boxShadow: '0px 0px 10px ' + color3 }"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import "@fontsource/saira-stencil-one";

import { useStore } from "@/stores/Store";
import { onMounted, onUnmounted, ref } from "vue";

const objectWidth = 14;
const objectHeight = 14;

const store = useStore();

let startTime = 0;

const startCounter = 30;
const counter = ref<number>(startCounter);
const hits = ref<number>(0);
const misses = ref<number>(0);

const board = ref<HTMLDivElement | null>(null);
const object1 = ref<HTMLDivElement | null>(null);
const object2 = ref<HTMLDivElement | null>(null);
const object3 = ref<HTMLDivElement | null>(null);

const objectImage1 = ref<HTMLImageElement | null>(null);
const objectImage2 = ref<HTMLImageElement | null>(null);
const objectImage3 = ref<HTMLImageElement | null>(null);

let counterInterval: null | number = null;

const timers: number[] = [];

const color1 = ref<string>("#FFF");
const color2 = ref<string>("#FFF");
const color3 = ref<string>("#FFF");
let colorInterval: null | number = null;

let lastId = 0;
let lastId1: number = lastId++;
let lastId2: number = lastId++;
let lastId3: number = lastId++;

async function onHit(index: number): Promise<void> {
  clearTimeout(timers[index]);
  moveObject(index);
  hits.value++;
  await store.trackPlayer.playHit();
}

async function onMiss(): Promise<void> {
  if (hits.value - misses.value > 0) {
    misses.value++;
  }
  await store.trackPlayer.playMiss();
}

const map: (null | number)[][] = [];

function moveObject(index: number): void {
  let availablePoints = getAvailablePoints();
  if (availablePoints.length === 0) {
    let deniedIds = [];
    if (index === 1) {
      deniedIds.push(lastId2);
      deniedIds.push(lastId3);
    } else if (index === 2) {
      deniedIds.push(lastId1);
      deniedIds.push(lastId3);
    } else if (index === 3) {
      deniedIds.push(lastId1);
      deniedIds.push(lastId2);
    }
    for (const i in map) {
      for (const k in map[i]) {
        const id = map[i][k];
        if (id !== null && !deniedIds.includes(id)) {
          map[i][k] = null;
        }
      }
    }
    availablePoints = getAvailablePoints();
  }
  if (availablePoints.length === 0) {
    alert("Ошибка! Нет мест для перемещения!");
    store.state = "finish";
    return;
  }
  const randomIndex = Math.floor(Math.random() * availablePoints.length);
  const object = availablePoints[randomIndex];
  for (let i = object[0]; i < object[0] + objectWidth; i++) {
    for (let k = object[1]; k < object[1] + objectHeight; k++) {
      if ((map[i] ?? null) === null) {
        map[i] = [];
      }
      if (index === 1) {
        map[i][k] = lastId;
      } else if (index === 2) {
        map[i][k] = lastId;
      } else if (index === 3) {
        map[i][k] = lastId;
      }
    }
  }

  let objectDiv: HTMLDivElement | null = null;

  if (index === 1) {
    objectDiv = object1.value;
  } else if (index === 2) {
    objectDiv = object2.value;
  } else if (index === 3) {
    objectDiv = object3.value;
  }

  if (objectDiv === null) {
    throw new Error();
  }

  objectDiv.style.left = object[0] * 10 + "px";
  objectDiv.style.top = object[1] * 10 + "px";

  let imageElement: HTMLImageElement | null = null;
  if (index === 1) {
    imageElement = objectImage1.value;
  } else if (index === 2) {
    imageElement = objectImage2.value;
  } else if (index === 3) {
    imageElement = objectImage3.value;
  }
  if (imageElement === null) {
    throw new Error();
  }
  imageElement.src = store.stickers.getRandom().element.src;

  let timeout;
  if (counter.value > (startCounter / 4) * 3) {
    timeout = 900 + Math.floor(Math.random() * 450);
  } else if (counter.value > (startCounter / 4) * 2) {
    timeout = 800 + Math.floor(Math.random() * 400);
  } else if (counter.value > startCounter / 4) {
    timeout = 700 + Math.floor(Math.random() * 350);
  } else {
    timeout = 600 + Math.floor(Math.random() * 300);
  }

  timers[index] = setTimeout(function (): void {
    moveObject(index);
  }, timeout);

  if (index === 1) {
    lastId1 = lastId++;
  } else if (index === 2) {
    lastId2 = lastId++;
  } else if (index === 3) {
    lastId3 = lastId++;
  }
}

function getAvailablePoints() {
  const div = board.value;
  if (div === null) {
    throw new Error();
  }

  const maxX = Math.floor(div.clientWidth / 10);
  const maxY = Math.floor(div.clientHeight / 10);

  const availablePoints = [];
  for (let i = 0; i < maxX - objectHeight; i++) {
    for (let k = 0; k < maxY - objectWidth; k++) {
      if (
        ((map[i] ?? null) === null || (map[i][k] ?? null) === null) &&
        ((map[i + objectWidth] ?? null) === null ||
          (map[i + objectWidth][k] ?? null) === null) &&
        ((map[i] ?? null) === null ||
          (map[i][k + objectHeight] ?? null) === null) &&
        ((map[i + objectWidth] ?? null) === null ||
          (map[i + objectWidth][k + objectHeight] ?? null) === null)
      ) {
        availablePoints.push([i, k]);
      }
    }
  }
  return availablePoints;
}

onMounted(function () {
  startTime = Date.now();
  moveObject(1);
  moveObject(2);
  moveObject(3);

  counterInterval = setInterval(function (): void {
    const interval = Math.floor((Date.now() - startTime) / 1000);
    if (interval >= startCounter) {
      const points = hits.value - misses.value;
      store.lastRating = points;
      if (points > store.topRating) {
        store.topRating = points;
        localStorage.setItem("rating", points.toString());
      }
      if (store.state !== "finish") {
        store.state = "finish";
      }
      return;
    }
    counter.value = startCounter - interval;
  }, 1000);
  colorInterval = setInterval(function (): void {
    color1.value = store.colors.getRandom();
    color2.value = store.colors.getRandom();
    color3.value = store.colors.getRandom();
  }, 300);
});

onUnmounted(function () {
  for (const i in timers) {
    clearTimeout(timers[i]);
  }
  if (counterInterval !== null) {
    clearInterval(counterInterval);
  }
  if (colorInterval !== null) {
    clearInterval(colorInterval);
  }
});
</script>

<style lang="scss">
@import "@/styles/common";

.board-page-object-container {
  width: 140px;
  height: 140px;
  position: absolute;
  display: flex;

  animation: stalker-shake 0.5s;
  animation-iteration-count: infinite;
  transition: box-shadow 0.3s ease;

  &-object {
    margin: 10px;
    width: 120px;
    height: 120px;
  }
}
</style>
